<template>
	<div class="SelectOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toCycleCount"></i><span>选择盘点单</span></header>
		<div class="container">

			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td  @click="SortArr('CycleCount_RFCountSel','CountNo','SortCondition1',$event)">盘点单号</td>
						<td  @click="SortArr('CycleCount_RFCountSel','CountType','SortCondition2',$event)">盘点类型</td>
						<td  @click="SortArr('CycleCount_RFCountSel','AddDate','SortCondition3',$event)">创建日期</td>
						<td  @click="SortArr('CycleCount_RFCountSel','AddWho','SortCondition4',$event)">创建人</td>
						<td  @click="SortArr('CycleCount_RFCountSel','CountStatus','SortCondition5',$event)">状态</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in CycleCount_RFCountSel'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.CountNo}}</td>
						<td>{{dataItem.CountType}}</td>
						<td>{{dataItem.AddDate}}</td>
						<td>{{dataItem.AddWho}}</td>
						<td>{{dataItem.CountStatus}}</td>
					</tr>
				</tbody>
			</table>

			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SelectOrder",
		data(){
			return{
				CycleCount_RFCountSel:[],//列表数据
				detail_item:0,//默认为第一个
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
			}
		},
		mounted(){
			$("thead").find("td").eq(0).siblings().append('<i class="iconfont"></i>')
		},
		created(){
			console.log(this.$store.state.CycleCount_RFCountSel)

			var CycleCount_RFCountSel = this.$store.state.CycleCount_RFCountSel;

			this.CycleCount_RFCountSel = CycleCount_RFCountSel;
		},
		methods:{
			// 返回上一页 盘点页面
			toCycleCount:function(){

				this.$router.push({
					name:"CycleCount",
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到盘点页面
			selectData_submit:function(){


				var CountNo = this.CycleCount_RFCountSel[this.detail_item].CountNo       //盘点单号
				var CountType =  this.CycleCount_RFCountSel[this.detail_item].CountType  //盘点类型

				this.$router.push({
					name:"CycleCount",
					query:{
						SelectOrder:{
							CountNo:CountNo,
							CountType:CountType
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.SelectOrder{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td{
						min-width: 160/50rem;
					}
					td:nth-child(1),td:nth-child(6){
						min-width: 110/50rem;
					}
					td:nth-child(4){
						min-width: 300/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
	@media screen and (max-width: 332px) and (min-width: 251px){
		.SelectOrder{
			&>.container{
				table{
					tr{
						td{
							min-width: 200/50rem;
						}
						td:nth-child(1),td:nth-child(6){
							min-width: 130/50rem;
						}
						td:nth-child(4){
							min-width: 360/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.SelectOrder{
			&>.container{
				table{
					tr{
						td{
							min-width: 260/50rem;
						}
						td:nth-child(1),td:nth-child(6){
							min-width: 160/50rem;
						}
						td:nth-child(4){
							min-width: 480/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


